@use "../component/navigation";
@use "../abstract/index" as *;

body {
  min-height: 100vh;
  font-family: "Barlow", sans-serif;
  background: url(../../images/crew/background-crew-mobile.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.container {
  max-width: 1110px;
  margin-inline: auto;
  text-align: center;
  padding-inline: 24px;
  padding-bottom: 42px;
}

.crew__subtitle {
  font-family: "Barlow Condensed", sans-serif;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  text-transform: uppercase;
  font-weight: normal;
  color: color(secondary-color);
}

.crew__subtitle--number {
  margin-right: 16px;
  opacity: 0.25;
  font-weight: 700;
}

.crew__image {
  border-bottom: 1px solid #383b4b;
  margin-top: 32px;
  margin-bottom: 32px;
  margin-inline: auto;
  padding-left: 15px;
  height: 223px;

  img {
    margin-inline: auto;
    height: 100%;
    display: block;
    backface-visibility: hidden;
  }
}

.crew__nav__list {
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 16px;
}

.crew__nav__link {
  cursor: pointer;
  padding: 0;
  display: block;
  width: 10px;
  height: 10px;
  border: none;
  background: color(secondary-color);
  border-radius: 50%;
  opacity: 0.17;
  transition: all 0.25s ease-in-out;

  &.active {
    opacity: 1;
  }

  &:hover,
  &:focus-visible {
    background: color(accent-color);
    opacity: 1;
  }
}

.crew__title {
  font-family: "Bellefair", sans-serif;
  color: color(secondary-color);
  text-transform: uppercase;
  margin-top: 32px;
}

.crew__title--commander {
  font-size: 16px;
  line-height: 18px;
  font-weight: normal;
  opacity: 0.5;
  margin-bottom: 8px;
}

.crew__title--name {
  font-size: 24px;
  line-height: 28px;
  font-weight: normal;
}

.crew__text {
  margin-top: 16px;
  font-family: "Barlow", sans-serif;
  font-size: 15px;
  line-height: 25px;
  color: color(accent-color);
}

@keyframes zoomIn {
  0% {
    transform-origin: center;
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}

@keyframes zoomIn__desktop {
  0% {
    transform-origin: center;
    transform: translate(100%, 0);
    opacity: 0;
  }
  100% {
    transform: translate(0, 0);
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    transform-origin: center;
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}

@keyframes fadeIn__desktop {
  0% {
    transform-origin: center;
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}

.zoomIn {
  animation: zoomIn 0.7s;
  @media (min-width: 1100px) {
    animation: zoomIn__desktop 0.7s;
  }
}

.fadeIn {
  animation: fadeIn 0.5s ease-in-out;
  @media (min-width: 1100px) {
    animation: fadeIn__desktop 0.5s;
  }
}

@media (min-width: 768px) {
  body {
    background: url(../../images/crew/background-crew-tablet.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  .container {
    display: grid;
    grid-template-areas: "subtitle" "content" "nav" "image";
    padding: 40px 39px 0;
  }

  .crew__subtitle {
    grid-area: subtitle;
    text-align: left;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 3.375px;
    margin-bottom: 60px;
  }

  .crew__content {
    grid-area: content;
  }

  .crew__title {
    margin-top: 0;
  }

  .crew__title--commander {
    font-size: 24px;
    line-height: 28px;
    margin-top: 0;
  }

  .crew__title--name {
    font-size: 40px;
    line-height: 46px;
  }

  .crew__text {
    margin-inline: auto;
    font-size: 16px;
    line-height: 28px;
    max-width: 458px;
    margin-bottom: 0;
  }

  .crew__nav {
    grid-area: nav;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .crew__nav__list {
    margin: 0;
  }

  .crew__image {
    align-self: end;
    grid-area: image;
    margin: 0;
    padding-left: 38px;
    max-height: 532px;
    border: none;
    overflow: hidden;
    height: 532px;

    img {
      // width: 415px;
      height: 100%;
    }
  }
}

@media (min-width: 1100px) {
  body {
    padding: 0.05px;
    background: url(../../images/crew/background-crew-desktop.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    overflow-x: hidden;
  }

  .container {
    grid-template-columns: auto auto;
    grid-template-rows: 202px 391px 109px;
    grid-template-areas: "subtitle image" "content image" "nav image";
    justify-content: space-between;
    padding: 62px 55px 0;

    @media (min-width: 1240px) {
      padding: 62px 0 0;
    }
  }

  .crew__subtitle {
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 4.725px;
    margin-top: 14px;
    margin-bottom: 0;
  }

  .crew__subtitle--number {
    margin-right: 24px;
  }

  .crew__title,
  .crew__text {
    text-align: left;
    margin-inline: 0 auto;
    margin: 0;
  }

  .crew__title--commander {
    font-size: 32px;
    line-height: 37px;
    margin-bottom: 16px;
  }

  .crew__title--name {
    font-size: 56px;
    line-height: 64px;
    margin-bottom: 28px;
  }

  .crew__text {
    font-size: 18px;
    line-height: 32px;
  }

  .crew__nav {
    margin: 0;
  }
  .crew__nav__list {
    justify-content: start;
    margin: 0;
    column-gap: 24px;
  }

  .crew__nav__link {
    width: 15px;
    height: 15px;
  }

  .crew__image {
    max-height: 100%;
    align-self: stretch;
    padding-left: 48px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    height: auto;
    img {
      width: 100%;
      max-width: 520px;
      height: auto;
    }
  }
}
